.__header {
    display: flex;
    flex-direction: row;
}

.__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: #939393;
    margin-top: 1.5rem;
}

.__header-content h1 span {
    color: #FF9E2C;
}

.__header-content .__region-card,
.__header-content .__person-card,
.__header-content .__date-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.__header-content .__region-card p,
.__header-content .__person-card p,
.__header-content .__date-card p {
    color: #000;
}

.__header-content .__region-card span,
.__header-content .__person-card span,
.__header-content .__date-card span {
    color: #919191;
    font-size: 1.1rem;
    font-family: var(--font-family);
}

.__upper-sec {
    display: flex;
    align-items: left;
    justify-content: left;
    gap: 1rem;
}

.__upper-sec img {
    width: 18px;
    height: 18px;
}

.__lower-sec {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    margin-top: 1rem;
}

.__lower-sec span {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.__header-content .__cards {
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 10px 30px rgba(169, 169, 169, 0.25);
    margin: 1rem;
    padding-bottom: 1rem;
    padding-left: .5rem;
    padding-right: .5rem;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
    flex-direction: row;
}

.__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    background: #FF4820;
    border: 2px solid #FF4820;
    padding: 0 1rem;
    color: #fff;
    cursor: pointer;
    outline: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    margin-top: 2rem;
}

.__header-content__people img {
    width: 220px;
    height: 58px;
}

.__header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #000;
    text-align: center;
}

.__header-content__people .__cont-happy-cust {
    display: flex;
    flex-direction: column;
    align-items: start;
}

.__header-content__people .__customer-ratings {
    display: flex;
    justify-content: center;
    align-items: baseline;
    gap: 1rem;
    margin: 0 0 0 1rem;
}

.__header-content__people .__cont-happy-cust p {
    color: #FF9E2C;
    font-size: 1.2rem;
    font-weight: bold;
}

.__header-content__people .__cont-happy-cust p span {
    color: #939393;
    font-weight: bold;
    margin-left: 20px;
}

.__header-content__people .__cont-happy-cust .__customer-ratings img {
    width: 16px;
    height: 16px;
}

.__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.__header-image img {
    width: 100%;
}

.__payments {
    margin-left: 5rem;
}

.__payments img {
    margin: 0;
    padding: 0;
    width: 30px;
    height: 20px;
}

.__upper-sec p {
    font-family: var(--font-family);
    color: #919191;
    font-style: italic;
    margin: -10px;
    padding: 0;
}

@media screen and (max-width: 1050px) {
    .__header {
        flex-direction: column;
    }
    .__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }
    .__header-content p {
        font-size: 16px;
        line-height: 24px;
    }
    .__header-content__people {
        flex-direction: column;
    }
    .__header-content__people p {
        margin: 0;
    }
    .__header-content__input input,
    .__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .__header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }
    .__header-content p {
        font-size: 14px;
        line-height: 24px;
    }
    .__header-content__input input,
    .__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
    .__header-content .__cards {
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    .__header-content__people img {
        width: 180px;
        height: 48px;
    }
    .__lower-sec img {
        width: 38px;
        height: 24px;
    }
    .__payments {
        margin-top: 2rem;
        margin-left: -1rem;
    }
    .__upper-sec p {
        font-size: 1rem;
        margin: -5px;
    }
    .__header-content__people .__cont-happy-cust p {
        margin: 1rem 0 0 0;
    }
    .__header-content__people .__cont-happy-cust p {
        font-size: 1rem;
    }
}