.__possibility {
    background-image: url(../../assets/Bg_downloadapp.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 90vw;
    display: flex;
    flex-direction: row;
}

.__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.__possibility-image img {
    width: inherit;
    height: inherit;
}

.__possibility-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}

.__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 48px;
    line-height: 30px;
    color: #fff;
    margin-bottom: 3rem;
}

.__playstore {
    display: flex;
}

@media screen and (max-width: 950px) {
    .__possibility {
        flex-direction: column;
    }
    .__possibility-image {
        margin: 1rem 0;
    }
    .__possibility-image img {
        width: unset;
        height: unset;
    }
    .__possibility-content {
        margin-top: 2rem;
    }
}

@media screen and (max-width: 700px) {
    .__possibility-image img {
        width: 100%;
        height: 100%;
    }
}

@media screen and (max-width: 400px) {
    .__possibility-content h4 {
        color: #000;
        font-size: 2rem;
        margin: 0 0 2rem 0;
    }
    .__playstore img {
        width: 120px;
        height: 60px;
    }
}