.__blog-container_article {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.__blog-container_article-image {
    width: 100%;
    height: 100%;
    border-radius: 20px;
}

.__blog-container_article-image img {
    border-radius: 20px;
    width: 100%;
    height: 100%;
}

.__blog-container_article-content {
    display: flex;
    justify-content: center;
    align-items: center;
}

.__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-size: 2rem;
    font-weight: 600;
    color: #fff;
    margin-top: -5rem;
    cursor: pointer;
    z-index: 99;
}

.__blog-container_article-content p:last-child {
    cursor: pointer;
}

@media screen and (max-width: 550px) {
    .__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25p;
    }
}