.__customers {
    margin: 2rem 0;
}

.__main-container {
    margin: 4rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.__main-container img {
    cursor: pointer;
}

.__content .__main-heading {
    font-family: var(--font-family);
    font-size: 2rem;
    font-weight: 800;
    margin-left: 15rem;
}

.__content .__main-heading span {
    color: #FF9E2C;
    font-size: 2rem;
}

.__upper-section .__content-cont {
    font-family: var(--font-family);
}

.__upper-section .__content-cont span {
    color: #939393;
}

.__upper-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 1rem;
}

.__upper-section .__img-cont img {
    width: 64px;
    height: 64px;
    border-radius: 50%;
}

.__card {
    border-radius: 20px;
    width: fit-content;
    padding: 1.3rem;
}

.__card .__middle-section p {
    width: 20ch;
    font-weight: 400;
    text-align: justify;
}

.__card .__lower-section p {
    font-size: .86rem;
    margin-left: auto;
    margin-top: 1rem;
}

.__card .__lower-section p img {
    width: 16px;
    height: 16px;
}

.switch-icon {
    width: 36px;
    height: 36px;
}

@media screen and (max-width: 490px) {
    .__content .__main-heading {
        margin-left: 1.45rem;
    }
    .__main-container {
        margin: 4rem auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .__main-container .switch-icon {
        display: none;
    }
}