.__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
}

.__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}

.__footer-btn p {
    font-size: 18px;
    font-family: var(--font-family);
    line-height: 21px;
    color: #fff;
    word-spacing: 2px;
}

.__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    text-align: left;
}

.__footer-links div {
    font-family: var(--font-family);
    width: 250px;
    margin: 1rem;
}

.__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.__footer-links_logo img {
    width: 200px;
    height: 160px;
    margin-bottom: 1rem;
}

.__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 15px;
    color: #fff;
}

.__footer-links_div {
    display: flex;
    flex-direction: column;
    align-items: left;
}

.__footer-links_div p {
    display: flex;
    align-items: start;
    gap: 1rem;
    cursor: pointer;
}

.__lower-cont {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    gap: 1rem;
}

.__lower-cont p {
    margin-left: -1rem;
    margin-right: 1rem;
}

.__footer-links_div h4 {
    font-size: 1.3rem;
    line-height: 17px;
    font-family: var(--font-family);
    color: #FF9E2C;
    margin-bottom: 2.5rem;
}

.__footer-links_div p {
    font-size: 14px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #FFF;
    margin-bottom: 0.9rem;
}

.__socials {
    display: block;
    padding-right: 8rem;
}

.__socials .__upper-section p {
    color: #fff;
    font-size: 1.3rem;
    font-weight: 400;
    margin-left: -12rem;
}

.__socials .__upper-section {
    display: flex;
    align-items: start;
}

.__socials .__lower-section {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    margin-left: -5rem;
}

.__socials .__lower-section img {
    width: 24px;
    height: 24px;
    aspect-ratio: 1 / 1;
    cursor: pointer;
}

.__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.__footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

.__footer-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
    display: flex;
    flex-direction: row;
}

.__footer-content__input input {
    outline: none;
    border: none;
    border-radius: 10px;
    padding: 1rem 1rem;
    margin-left: -.85rem;
}

.__footer-content__input button img {
    width: 24px;
    height: 24px;
    aspect-ratio: 1 / 1;
}

.__footer-content__input button {
    border-radius: 10px;
    background-color: #FF9E2C;
    padding: .70rem;
    margin-left: 1rem;
    outline: none;
    border: none;
    cursor: pointer;
}

@media screen and (max-width: 850px) {
    .__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
    .__footer-links div {
        margin: 1rem 0;
    }
    .__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
    .__lower-cont p {
        margin-left: 0;
        margin-right: 0;
    }
    .__footer-content__input input {
        margin-left: -.25rem;
    }
    .__socials .__upper-section p {
        margin-left: -10rem;
    }
}