.__popular {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.__content {
    padding-right: 1rem;
}

.__content h2 {
    font-size: 36px;
    font-weight: 700;
    font-family: var(--font-family);
    color: #000;
}

.__content h2 span {
    color: #FF9E2C;
}

.__content button {
    color: #fff;
    background-color: #FF9E2C;
    padding: .55rem 2rem;
    margin-top: 1rem;
    border-radius: 20px;
    border: none;
}

.__card {
    background-color: #fff;
    box-shadow: 0px 10px 30px rgba(169, 169, 169, 0.25);
    border-radius: 20px;
    width: 350px;
    margin: 1rem;
}

.__up-section {
    display: flex;
    flex-direction: column;
    justify-content: space-around !important;
    padding: 1rem 1.25rem 1rem 1.25rem;
}

.__card img {
    width: 350px;
}

.__card p {
    font-family: var(--font-family);
    font-size: 1.3rem;
    font-weight: 600;
    color: #000;
}

.__card h1 {
    font-family: var(--font-family);
    font-size: 1rem;
    color: #939393;
    margin: .5rem 0;
}

.__card h1 span {
    font-family: var(--font-family);
    color: #FF9E2C;
}

.__card h2 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 1.35rem;
    margin: 0.25rem 0 1rem 0;
}

.__lower-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: .55rem;
    padding: 0 1rem 1rem 1rem;
}

.__lower-section .__img-cont {
    font-family: var(--font-family);
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.__lower-section .__img-cont img {
    width: 48px;
    height: 48px;
}

.__lower-section p img {
    width: 16px;
    height: 16px;
}

@media screen and (max-width: 650px) {
    .__popular {
        flex-direction: column;
    }
    .__card img {
        width: 250px;
    }
    .__lower-section .__img-cont img {
        width: 36px;
        height: 36px;
    }
    .__lower-section .__img-cont span {
        font-size: .70rem;
    }
}