.__blog {
    background-color: #f8f8f8;
    display: flex;
    flex-direction: column;
}

.__blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.__blog-heading h1 {
    font-size: 48px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}

.__blog-heading h1 img {
    margin-bottom: 1.1rem;
    margin-right: 1rem;
}

.__blog-heading h1 span {
    color: #FF9E2C;
}

.__blog-container {
    display: flex;
    flex-direction: row;
}

.__blog-container_groupA {
    flex: 0.75;
    margin-right: 2rem;
}

.__blog-container_groupB {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .__blog-container {
        flex-direction: column-reverse;
    }
    .__blog-container_groupA {
        margin: 2rem 0;
    }
    .__blog-container_groupA .__blog-container_article {
        width: 48%;
    }
    .__blog-container_groupA .__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }
    .__blog-container_groupA .__blog-container_article {
        width: 100%;
    }
    .__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
}